import React, { useEffect, useState } from "react"

import { makeStyles } from "@material-ui/core/styles"

import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import Snackbar from "@material-ui/core/Snackbar"
import MuiAlert from "@material-ui/lab/Alert"
import InfoIcon from "@material-ui/icons/Info"
import IconButton from "@material-ui/core/IconButton"
import Modal from "@material-ui/core/Modal"
import Typography from "@material-ui/core/Typography"

import Networking from "../networking"

const useStyles = makeStyles((theme) => ({
  motorTempInfoButton: {
    paddingBottom: 1,
    paddingTop: 0,
    marginLeft: theme.spacing(1),
    color: "white",
  },
  motorTempInfoCard: {
    width: 400,
  },
  snackbar: {
    opacity: 0.85,
  },
}))

interface Props {
  networking: Networking
}

export default function Component({ networking }: Props) {
  const classes = useStyles()

  const [maxMotorTemp, setMaxMotorTemp] = useState(0)
  const [snackbarOpen, setSnackbarOpen] = useState(false)
  const [modalOpen, setModalOpen] = useState(false)

  // handleMotorTemperature()
  useEffect(() => {
    const temperature_threshold = 85

    const mcs = networking.robotState.motorControllers
    const max_t = Math.max(
      mcs.leftBack.windingTemperature,
      mcs.leftFront.windingTemperature,
      mcs.rightBack.windingTemperature,
      mcs.rightFront.windingTemperature
    )
    setMaxMotorTemp(max_t)

    if (max_t > temperature_threshold) {
      setSnackbarOpen(true)
    } else {
      setSnackbarOpen(false)
    }
  })

  return (
    <div>
      <Modal
        style={{ display: "flex", alignItems: "center", justifyContent: "center" }}
        open={modalOpen}
        onClose={() => setModalOpen(false)}
      >
        <Card className={classes.motorTempInfoCard} variant="outlined">
          <CardContent>
            <Typography variant="body1">
              Upon reaching the temperature threshold, if Stride is on a slope, it will move downhill until
              flat ground. <br />
              This feature is omitted in the "E-stopped" state, in which Stride will attempt to stay still
              until the motors give out.
            </Typography>
          </CardContent>
        </Card>
      </Modal>

      <Snackbar className={classes.snackbar} open={snackbarOpen}>
        <MuiAlert elevation={6} variant="filled" severity="warning">
          Motor Temp: {maxMotorTemp} / 105C
          <IconButton
            className={classes.motorTempInfoButton}
            size="small"
            onClick={() => {
              setModalOpen(true)
            }}
          >
            <InfoIcon />
          </IconButton>
        </MuiAlert>
      </Snackbar>
    </div>
  )
}
